<script>

// import "https://player.twitch.tv/js/embed/v1.js";
// import * as renderMain from '../assets/js/renderMain.js'
import { Swiper } from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'

export default {
  name: 'GamesList',
  components: {
    Swiper,
    // SwiperSlide
  },
  data() {
    return {
      swiperOption: {
        slidesPerView: 'auto',
        spaceBetween: 0,
        navigation: {
          nextEl: '.slider__right',
          prevEl: '.slider__left'
        }
      },
      window: {
        width: 0,
        height: 0
      },
      tooltipBreakPoint: 400,
      tooltipHeight: 0,
    }
  },
  created() {
    window.addEventListener('resize', this.handleResize);
    this.handleResize();
  },
  destroyed() {
    window.removeEventListener('resize', this.handleResize);
  },
  methods: {
    handleResize() {
      this.window.width = window.innerWidth;
      this.window.height = window.innerHeight;
    },
    // getPosition(event) {
    //   // clientX/Y gives the coordinates relative to the viewport in CSS pixels.
    //   console.log(event.clientX);
    //   console.log(event.clientY);

    //   // // pageX/Y gives the coordinates relative to the <html> element in CSS pixels.
    //   // console.log(event.pageX);
    //   // console.log(event.pageY);

    //   // // screenX/Y gives the coordinates relative to the screen in device pixels.
    //   // console.log(event.screenX);
    //   // console.log(event.screenY);
    // }
    // addTwitchPlayer: function() {
    //   var options = {
    //     width: 400,
    //     height: 300,
    //     channel: "twitchpresents"
    //   }
    //   var player = new Twitch.Player("SamplePlayerDivID", options);

    //   player.addEventListener(Twitch.Player.READY, () => {
    //     alert("Player is ready!!");
    //   });
    // }
  },
  async mounted() {
    const roomMembersImagesLinks = ((ctx) => ctx.keys().map(ctx))(
      require.context("../assets/img/room/members", true, /.*/)
    );

    const populate = (optionsObj) => {
      const listClass = optionsObj.listClass;
      const itemClass = optionsObj.itemClass;
      const count = optionsObj.count;
        
      const list = this.$el.querySelectorAll(`.${listClass}`)[0];
      const item = this.$el.querySelectorAll(`.${itemClass}`)[0];
      if (!list || !item) { return }
      for (let i = 0; i < count; i++) {
        const newItem = item.cloneNode(true);
        
        if (listClass === 'room__members-list') {
          const roomMemberAvatar = newItem.querySelectorAll(".room__member img")[0];
          let imgIndex = Math.floor(Math.random() * 37);
          roomMemberAvatar.src = `${roomMembersImagesLinks[imgIndex]}`;
          roomMembersImagesLinks;
        }

        if (listClass === 'center__slider-container') {
          const img = newItem.querySelectorAll(".frame img")[0];
          let imgIndex = Math.floor(Math.random() * 9999);
          img.src = `https://source.unsplash.com/random?sig=${imgIndex}`;
        }

        list.appendChild(newItem);
      }
    };
    await populate({
      listClass: "center__slider-container",
      itemClass: "slider__item",
      count: 9,
    });
    await populate({
      listClass: "room__members-list",
      itemClass: "room__member",
      count: 9,
    });

    const populateRooms = () => {
      const gamesList = this.$el.querySelector(".games__list");
      if (!gamesList) { return }
      const room = this.$el.querySelectorAll(".room")[0];
      const gamesSeparator = this.$el.querySelectorAll(".games__separator")[0];
      for (let i = 0; i < 20; i++) {
        const newRoom = room.cloneNode(true);
        const newSeparator = gamesSeparator.cloneNode(true);
        const roomTitle = newRoom.querySelector(".room__title");
        roomTitle.textContent = ` ${i + 1}`;
        const roomMemberAvatars = newRoom.querySelectorAll(".room__member img");
        roomMemberAvatars.forEach((roomMemberAvatar) => {
          let imgIndex = Math.floor(Math.random() * 37);
          roomMemberAvatar.src = `${roomMembersImagesLinks[imgIndex]}`;
          roomMembersImagesLinks;
        });  

        gamesList.appendChild(newRoom);
        gamesList.appendChild(newSeparator);
      }
    };
    await populateRooms();

    this.slider = new Swiper(this.$refs.mySlider, this.mySliderOptions);
    
    const tooltipElements = this.$el.querySelectorAll(".tooltip");
    tooltipElements.forEach((tooltipElement) => {
      tooltipElement.addEventListener("mouseover", () => {
        let clientRect = tooltipElement.getBoundingClientRect();
        let clientY = clientRect.top;
        // console.log(clientY);
        let tooltipTextElement = tooltipElement.querySelector(".tooltiptext");
        let tooltipTextContainer = tooltipElement.querySelector(".tooltiptext__container");
        let tooltipTextElementHeight = tooltipTextContainer.getBoundingClientRect().height;
        // console.log(tooltipTextElementHeight);
        this.tooltipHeight = tooltipTextElementHeight - 52;
        if ((this.window.height - clientY) < this.tooltipBreakPoint) {
          tooltipTextElement.style.top = `-${this.tooltipHeight}px`;
        } else {
          tooltipTextElement.style.top = `0px`;
        }
      });
    });

    const tooltipBtnElements = this.$el.querySelectorAll(".tooltip-btn");
    tooltipBtnElements.forEach((tooltipBtnElement) => {
      tooltipBtnElement.addEventListener("mouseover", () => {
        let clientRect = tooltipBtnElement.getBoundingClientRect();
        let clientY = clientRect.top;
        let tooltipTextElement = tooltipBtnElement.querySelector(".tooltiptext-btn");
        let tooltipTextContainer = tooltipBtnElement.querySelector(".tooltiptext__container");
        let tooltipTextElementHeight = tooltipTextContainer.getBoundingClientRect().height;
        // console.log(tooltipTextElementHeight);
        this.tooltipHeight = tooltipTextElementHeight - 48;
        if ((this.window.height - clientY) < this.tooltipBreakPoint) {
          tooltipTextElement.style.top = `-${this.tooltipHeight}px`;
          tooltipTextElement.style.left = `-${this.tooltipWidth}px`;
        } else {
          tooltipTextElement.style.top = `0px`;
        }
      });
    });
  },

}
</script>

