<template>
  <div class="center-wrapper__center center">
    <div class="center__profile profile">
      <div class="profile__background-wrapper">
        <div class="profile__background-image-wrapper">
          <img class="profile__background-image" src="https://source.unsplash.com/random" alt="">
        </div>
        <div class="profile__background-image-mask">
        </div>
        <div class="profile__header">
          <!-- <div class="profile__header-left">
            <a href="#" class="profile__header-status-button _online">
              
            </a>
          </div> -->
        </div>
      </div>
      <div class="profile__content">
        <div class="profile__content-side">
          <img class="profile__avatar" src="../assets/img/profile/profile-avatar.png">
          <div class="profile__actions-below-avatar actions">
            <a href="#" class="actions__item actions__add">
              <img src="../assets/img/profile/AddContact.svg">
            </a>
            <a href="#" class="actions__item actions__message">
              <img class="actions__message-icon" src="../assets/img/profile/message-icon.svg" alt="">
              
            </a>
          </div>
          <div class="profile__tabs-links">
            <a href="#" class="profile__tabs-links-item profile__tabs-item_active">
              
            </a>
            <a href="#" class="profile__tabs-links-item">
              
            </a>
            <a href="#" class="profile__tabs-links-item">
              
            </a>
          </div>
        </div>
        <div class="profile__content-main">
          <div class="profile__info-main main-info">
            <div class="main-info__left">
              <div class="profile__name">
                 
              </div>
              <div class="main-info__below-name">
                <div class="main-info__primary-stats">
                  <div class="main-info__primary-label"> </div>
                  <div class="main-info__primary-value">562</div>
                </div>
              </div>
            </div>
          </div>
          <div class="main-info__right">
          </div>
          <div class="profile__friends-brief content-block">
            <div class="profile__friends-brief-container">
              <a href="#" class="profile__friends-brief-item">
                <img src="../assets/img/group/3.png" alt="">
              </a>
            </div>
            <a href="#" class="profile__friends-brief-button">
                57
            </a>
          </div>
          <div class="profile__tab tab content-block">

          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import * as renderMain from '../assets/js/renderMain.js'

export default {
  name: 'Profile',
  async mounted() {
    const briefFriendsImagesLinks = ((ctx) => ctx.keys().map(ctx))(
      require.context("../assets/img/friends", true, /.*/)
    );

        const populate = (optionsObj) => {
          const listClass = optionsObj.listClass;
          const itemClass = optionsObj.itemClass;
          const count = optionsObj.count;
            
          const list = this.$el.querySelectorAll(`.${listClass}`)[0];
          const item = this.$el.querySelectorAll(`.${itemClass}`)[0];
          if (!list || !item) { return }
          for (let i = 0; i < count; i++) {
            const newItem = item.cloneNode(true);
            
            if (listClass === 'room__members-list') {
              const roomMemberAvatar = newItem.querySelectorAll(".room__member img")[0];
              let imgIndex = Math.floor(Math.random() * 37);
              roomMemberAvatar.src = `${briefFriendsImagesLinks[imgIndex]}`;
              briefFriendsImagesLinks;
            }

            if (listClass === 'center__slider-container') {
              const img = newItem.querySelectorAll(".frame img")[0];
              let imgIndex = Math.floor(Math.random() * 9999);
              img.src = `https://source.unsplash.com/random?sig=${imgIndex}`;
            }

            list.appendChild(newItem);
          }
        };
        populate({
          listClass: "profile__friends-brief-container",
          itemClass: "profile__friends-brief-item",
          count: 9,
        });


  },
}
</script>

